.YPath {
  text-align: center;
}

.YPath-logo {
  -webkit-animation: YPath-logo-spin infinite 20s linear;
          animation: YPath-logo-spin infinite 20s linear;
  height: 80px;
}

.YPath-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.YPath-title {
  font-size: 1.5em;
}

.YPath-intro {
  font-size: large;
}

@-webkit-keyframes YPath-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes YPath-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


body, html {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}


